import React from 'react';
import logo from './logo.svg';
import './App.css';


class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			records: {},
			isLoaded: false, 
		}
	}
	
	componentDidMount() {
		fetch('https://europe-west1-freesolarcalc.cloudfunctions.net/wave_max/?lat=4.0&lon=1.3')
		.then(res => res.json())
		.then(json => {
			this.setState({
				isLoaded: true,
				records: json, 
			})
		});
	}
	
	render() {
		//var {isLoaded, records} = this.state;
		
	  	return (
    			<h1>APP</h1>
	  	);
	}
}

export default App;


